@use 'forward' as *;

/*================== Slider ====================== */

/* Slideshow container */
.slider {
    position: relative;

    width: 100%;
    @include padding-bottom(3.6rem);
}

.slide {
    position: relative;

    width: 100%;
    transition: opacity 0.6s ease-in-out;
    aspect-ratio: var(--aspect-ratio, 16/9);
}

.slide-content {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
}

/* When a slide is visible */
.slide.active {
    display: block;
}

.slide img,
.slide iframe {
    display: block;

    width: 100%;
    height: auto;
}

/* Next & previous buttons */
.prev,
.next {
    position: absolute;
    z-index: -5;
    top: 50%;

    cursor: pointer;
    user-select: none;
    transform: translateY(-50%);
    transition: color 0.4s ease-in-out,
                padding 0.4s ease-in-out,
                padding-left .4s ease-in-out,
                padding-right .4s ease-in-out,
                opacity 0.5s ease-in-out,
                left .4s ease-in-out,
                right .4s ease-in-out,
                background-color .4s ease-in-out;
    color: #717171;
    background-color: rgba(0, 0, 0, .25);
    @include font($ff-1, 1.8rem, 600, 1em);
    @include padding(1.5rem);
    &:hover {
        color: white;
        background-color: rgba(0, 0, 0, .8);
    }
    &.ll-fade {
        opacity: 0;
        &.vis {
            opacity: 1;
        }
    }
}

/* Position the "next button" to the right */
.next {
    border-radius: 0 .5rem .5rem 0;
    @include rfs(-4rem, right);
    @include padding-left(2rem);
}

/* Position the "next button" to the right */
.prev {
    border-radius: .5rem 0 0 .5rem;
    @include rfs(-4rem, left);
    @include padding-right(2rem);
}

.slider-dots {
    position: absolute;
    z-index: 10;
    left: 50%;
    bottom: -0.2rem;

    display: flex;
    transition: left 0.4s ease-in-out,
                transform .4s ease-in-out;
    transform: translateX(-50%);

    @include rfs(0.5rem, gap);
}

/* The dots/bullets/indicators */
.dot {
    display: inline-block;

    width: 1.2rem;
    height: 1.2rem;
    margin: 0 .5rem;
    margin: 0 2px;

    cursor: pointer;
    transition: background-color .6s ease;

    border-radius: 10%;
    background-color: #bbb;
}

.dot.active,
.dot:hover {
    background-color: #717171;
}

/* Fading animation */
.fade {
    -webkit-animation-name: fade;
    animation-name: fade;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
}

@-webkit-keyframes fade {
    from {
        opacity: .75;
    }

    to {
        opacity: 1;
    }
}

@keyframes fade {
    from {
        opacity: .75;
    }

    to {
        opacity: 1;
    }
}

@media only screen and (max-width: 650px) {
   /* Position the "next button" to the right */
    .prev {
        z-index: 5;
        left: 0rem;
        background-color: rgba(0, 0, 0, 0);
    }

    .next {
        z-index: 5;
        right: 0rem;
        background-color: rgba(0, 0, 0, 0);
    }

    .prev:hover,
    .next:hover {
        color: white;
        background-color: rgba(0, 0, 0, 0);
    }
}


@media only screen and (max-width: 850px) {
    .slider-dots { 
        left: 5%;
        transform: translateX(0);
    }
}
