@use 'forward' as *;

/* ======================== Lightbox ============================ */

#lightbox {
    display: flex;
    flex-direction: column;

    position: fixed;
    z-index: 9999999;
    top: 0;
    left: 0;

    visibility: hidden;
    box-sizing: border-box;
    width: 100%;
    height: 100%;

    align-items: center;
    justify-content: center;

    cursor: pointer;
    transition: opacity .5s ease, visibility .5s ease;

    opacity: 0;
    background: rgba(0, 0, 0, .85);

    backdrop-filter: blur(2rem);
}

#lightbox.active {
    visibility: visible;

    opacity: 1;
}

#lightbox .img {
    position: absolute !important;
    top: 50% !important;
    left: 50% !important;

    width: 85vw !important;
    height: auto !important;

    margin: 0 auto;
    align-items: center;
    justify-content: center;

    -webkit-transform: translate(-50%, -50%) !important;
    -ms-transform: translateX(-50%) translateY(-50%) !important;
    transform: translate(-50%, -50%) !important;

    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

#lightbox .img img {
    display: block;
    width: auto;
    max-width: 100%;
    max-height: 90vh;
    margin: auto;

    transition: opacity .5s ease;
}

#lightbox .img.loaded img {
    opacity: 1;
}

#lightbox .videoWrapperContainer {
    position: absolute !important;
    top: 50% !important;
    left: 50% !important;

    width: 85vw !important;
    height: auto !important;

    margin: 0 auto;

    -webkit-transform: translate(-50%, -50%) !important;
    -ms-transform: translateX(-50%) translateY(-50%) !important;
    transform: translate(-50%, -50%) !important;

    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

#lightbox .videoWrapper {
    width: auto;
    max-height: 85vh;

    line-height: 0;

    margin: 0 auto;

    background: rgb(0, 0, 0);
}

#lightbox .videoWrapper iframe {
    display: block;
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    transition: opacity .5s ease;

    border: 0;
}

#lightbox .videoWrapper.loaded iframe {
    opacity: 1;
}

#lightbox span.caption {
    opacity: 0;
    position: absolute;
    display: block;
    transition: opacity .5s ease;
    border-radius: .35rem;
    color: rgba(171, 190, 200, 0.5);
    width: auto;
    margin: 0 auto;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    @include padding(1.5rem);
    @include font($ff-base, $fs-base, 400, 1.35em);
}

#lightbox.active span.caption {
    opacity: 1;
}

#lightbox span.caption::before {
    content: "";
    position: absolute;
    top: 2rem;
    left: 1rem;
    right: 1rem;
    bottom: 2rem;
    border-radius: 5rem;
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 0.5rem 3rem 1rem rgba(0, 0, 0, 0.1);
    z-index: -1;
}
a.lightbox-vimeo {
    pointer-events: auto;
}

a.lightbox-vimeo * {
    pointer-events: none;
}

#lightbox #prev,
#lightbox #next {
    position: fixed;
    z-index: 99;
    top: 50%;

    display: block;

    height: auto;
    margin: auto;

    cursor: pointer;
    text-decoration: none;

    color: rgb(140, 145, 150);
    transition: color .4s ease;
    @include font($ff-base, 6rem, 600, 0.5em);
    @include padding(0 2.5rem);
}

#lightbox #prev:hover,
#lightbox #next:hover {
    color: rgb(250, 250, 250);
}

#lightbox #prev {
    left: 0;
}

#lightbox #next {
    right: 0;
}

#lightbox #close {
    position: fixed;
    z-index: 99;
    top: 0;
    right: 0;

    width: 5rem;
    height: 5rem;

    cursor: pointer;
    text-decoration: none;
}

#lightbox #close:after,
#lightbox #close:before {
    position: absolute;

    width: 2.5rem;
    height: 0.5rem;
    margin-top: 2.2rem;
    margin-left: 1.4rem;

    content: '';
    /* Safari */
    -webkit-transform: rotate(-45deg);
    /* Firefox */
    -moz-transform: rotate(-45deg);
    /* IE */
    -ms-transform: rotate(-45deg);
    /* Opera */
    -o-transform: rotate(-45deg);
    -webkit-transform-origin: 50% 50%;
    -moz-transform-origin: 50% 50%;
    -o-transform-origin: 50% 50%;
    transform-origin: 50% 50%;

    background: white;
}

#lightbox #close:after {
    /* Safari */
    -webkit-transform: rotate(45deg);
    /* Firefox */
    -moz-transform: rotate(45deg);
    /* IE */
    -ms-transform: rotate(45deg);
    /* Opera */
    -o-transform: rotate(45deg);
}

#lightbox,
#lightbox * {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
