@use 'forward' as *;

/* ======================== Encrypted ============================ */

#encrypted-container {
    position: relative;
    display: flex;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    @include margin(2.4rem 0);
}

#decrypt-form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 30rem;
    justify-content: center;
    align-items: center;
    @include margin(1.5rem auto);
    @include padding(1.5rem);
}

#decrypt-field {
    line-height: 1.5em;
    text-align: center;
    width: 100%;
    height: $space-M;

    transition: border .4s ease;

    background-color: $c-bg;

    border: 0.3rem solid #d0d0d0;
    border-radius: 0.6rem;
    outline: none;

    @include padding(.6rem 1.2rem);

    @include font($ff-3, 2rem, 400, 2em);
}

#decrypt-field:hover {
    border-color:#e3e6eb;
}

#decrypt-field:active,
#decrypt-field:focus,
#decrypt-field:visited {
    border-color: #43464d;
}

#decrypt-field::placeholder {
    color:rgba(50, 52, 58, 0.3);
    @include font($ff-3, 1.6rem, 400, 2rem);
}

#decrypt-btn {
    transition: background-color .4s ease, color .4s ease, border-radius .4s ease;

    color: rgb(208, 208, 208);
    background-color:rgb(93, 93, 93);

    border-radius: 0.5rem;
    border: none;
    text-align:center;

    @include font($ff-base, 1.6rem, 600, 1em);
    @include rfs(0.1rem, letter-spacing);
    @include margin-top(1.5rem);
    @include padding(0.8rem 1.2rem);
}

#decrypt-btn:active,
#decrypt-btn:focus {
    background-color:rgb(67, 70, 77);
}

#decrypt-btn:hover {
    color:rgb(255, 255, 255);
    background-color:rgb(50, 52, 58);
    border-radius: 1rem;
}
