@use 'forward' as *;

/* Ensure space between each container */
.container {
    width: 100%;
    min-height: 100%;
    flex: 1 0 auto;
    transition: opacity 0.5s ease-in-out;
    @include margin-bottom($space-L);
	&.media {
    	@include margin-bottom($space-M);
    }
    &.details {
		@include margin-bottom($space-L);
	}
	&.scroll-fade {
  		opacity: 0;
  		body.no-js & {
    		opacity: 1;
  		}
  		&.vis {
    		opacity: 1;
  		}
	}
}

.pdf-wrapper {
  aspect-ratio: 45 / 59;
  width: clamp(70rem, 100%, 90rem);
  max-width: calc(100vw - 3rem);
}

object.pdf-embed {
  width: 100%;
  height: 100%;
  border: none;
  display: block;
}

.caption {
    position: relative;

    width: 100%;

    text-align: right;

    color: rgb(130, 130, 130);
    @include margin-top(2rem);
    @include font($ff-base, $fs-base, 400, 1.2em);
}

#project-header {
	display: flex;
	flex-direction: column;
	white-space: nowrap;
	@include margin-bottom($space-M);
}

.project-title-wrap {
	display: flex;
	flex-direction: column;
	@include margin-top($space-S);
}

.project-description {
	display: flex;
	white-space: normal;
	width: 90%;
	height: auto;
	align-content: center;
	line-height: 1.5em;
	border-right: none;
	border-bottom: none;
	border-top: none;
	border-style: solid;
	border-color:rgba(60,60,60,0.25);
	@include rfs(0 0 0 1.8rem, border-width);
	@include padding-left($space-XS);
}

.project-details-wrap {
	display: flex;
	flex-direction: row;
	@include margin-top($space-M);
	.created-with {
		@include margin-right($space-M);
	}
	.credits {
		white-space: normal;
	}
}

#grid-container {
    position: relative;

    display: grid;

    width: 100%;
    max-width: 100%;
    height: 100%;

    text-align: left;

    grid-template-rows: auto;
    grid-template-columns: 1fr 1fr;
    place-content: stretch;
    place-items: stretch stretch;
    @include rfs($space-M, grid-gap);
}

.project-card {
	display: flex;
	flex-direction: column;
}

.project-card a{
	position: relative;
	margin: auto;
}

#view-project-overlay {
	display: flex;
	position: absolute;
	top: 50%;
	left: 50%;
	width: 100%;
	height: 100%;
	align-items: center;
  	justify-content: center;
	transform: translate(-50%, -50%);
	text-transform: uppercase;
	text-align: center;
	z-index: 100;
	opacity: 0;
	color: #fff;
	background: rgba(0,0,0,0);
	transition: opacity 0.3s ease, background 0.5s ease;
	white-space: normal;
	@include font($ff-1, $fs-mid, 600);
}

/* On hover, the overlay becomes visible */
.project-card a:hover #view-project-overlay {
  opacity: 1;
  background: rgba(0,0,0,0.6);
}

.project-tagline {
	@include margin-top($space-XS);
}

.project-post-row {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.project-post-l {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: left;
	white-space: nowrap;
	@include margin(1rem 0);
}

.project-post-title {
	margin: auto;
	@include font($ff-1, 3rem, 600, 1.5em);
}

.project-post-r {
	display: flex;
	flex-direction: column;
	justify-content: center;
	border-left: 0.1rem solid black;
	width: 100%;
	max-width: 80%;
	@include margin(0 0 0 4rem);
}

.proj-btn {
    justify-content: center;
    text-align: center;
    text-transform: uppercase;

    transition: background-color .4s ease, color .4s ease, border-radius .4s ease;

    color: rgb(208, 208, 208);
    background-color:rgb(93, 93, 93);

    @include font($ff-2, $fs-base, 500, 1.35em);
    @include rfs(0.6rem, border-radius);
    @include margin(1.5rem);
    @include padding(1.2rem);
}

.proj-btn:active,
.proj-btn:focus {
    background-color:#43464d
}

.proj-btn:hover {
    color:#fff;
    background-color:#32343a
}

@media only screen and (max-width: 650px) {

	.project-post-row {
		flex-direction: column;
		align-items: flex-start;
		text-align: center;
	}

	.project-post-l {
		justify-content: flex-start;
		align-items: center;
		margin: auto 0;
		flex: 1 0 auto;
	}

	.project-post-r {
		border-left: 0.1rem solid rgba(0, 0, 0, 0);
		text-align: left;
		margin: 0;
		width: 100%;
		white-space: normal;
		margin: 0;
	}
}
