@use 'forward' as *;

/* ============================= Header ================================ */

#site-header {
    position: sticky;
    z-index: 1000;
    top: 0;
    width: 100%;
    /*min-width: 45rem;*/
    margin: 0 auto;
    background: rgba(22, 18, 24, 1);
    @include padding(3rem 0);
    will-change: transform, opacity, padding;
    transition: box-shadow 0.3s cubic-bezier(0.7, 0.04, 0.6, 0.8),
                transform 0.5s cubic-bezier(0.77, 0.03, 0.54, 0.92),
                background 0.4s cubic-bezier(0.7, 0, 0.68, 1),
                padding 0.4s cubic-bezier(0.65, 0.1, 0.7, 0.9),
                opacity 0.6s ease-in-out;
    &.finish {
        transform: translateY(0) !important;
        opacity: 1 !important;
        box-shadow: rgba(0, 0, 0, 0.6) 0 2rem 3rem -1rem,
                    rgba(0, 0, 0, 0.29) 0.6rem 0.2rem 1.6rem 0 !important;
    }
    &.slide-out {
        transform: translateY(-100%) !important;
        opacity: 1 !important;
        box-shadow: rgba(8, 10, 9, 0.05) 0 0.3rem 1.2rem 0,
                rgba(6, 12, 11, 0.04) 0 0.1rem 1rem 0 !important;
    }
    &.init {
        transform: translateY(-100%);
        opacity: 0;
        box-shadow: rgba(0, 0, 0, 0) 0 0 0 0,
                rgba(0, 0, 0, 0) 0 0 0 0;
    }
}

.header-wrap {
    display: flex;
    flex-direction: row;
    width: 80%;
    /*min-width: 45rem;*/
    max-width: 90rem;
    margin: 0 auto;
    text-transform: uppercase;
    justify-content: space-between;
    align-items: center;
    @include padding(1rem 0 1.5rem 0);
    transition: max-width .3s ease,
                padding .3s ease-in-out,
                filter 0.45s ease-in-out,
                opacity 0.45s cubic-bezier(0.7, 0, 0.68, 1);
    &.finish {
        opacity: 1 !important;
        filter: blur(0rem) !important;
    }
    &.slide-out {
        opacity: 0 !important;
        filter: blur(0.5rem) !important;
    }
    &.init {
        filter: blur(0.5rem);
        opacity: 0;
    }
    > .header-title-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        @include font-size($fs-head);
        .site-title,
        .site-tagline {
            width: 100%;
            margin: 0;
            padding: 0;
            display: block;
            text-align: center;
            white-space: nowrap;
            text-transform: uppercase;
            text-shadow: 0.1rem 0.15rem 0.3rem rgba(0, 0, 0, 0.8);
            transition: text-shadow 0.4s ease-in-out, line-height 0.5s cubic-bezier(0.7, 0, 0.68, 0.75), filter 0.45s ease-in-out, color 0.4s ease-in-out;
        }
    }
}

.site-title {
    color: $c-1;
    @include font($ff-1, $fs-head, 700, 1em);
    &:hover,
    &:focus {
        color: hsl(219, 17%, 32%);
        text-shadow: 0.2rem 0.4rem 0.5rem rgba(0, 0, 0, 1);
    }
    .hue-rotate {
        span {
            transition: filter 0.25s ease-out;
        }
    }
}

.site-tagline {
    color: rgba(144, 150, 173, 1);
    letter-spacing: clamp(0.21rem, calc(0.21rem + 0.00333 * (100vw - 45rem)), 0.46rem);
    align-items: center;
    justify-content: center;
    @include font($ff-2, 1.7rem, 400, 1em);
    &:hover,
    &:focus {
        color: hsl(228, 17%, 55%);
        text-shadow: 0.2rem 0.4rem 0.5rem rgba(0, 0, 0, 1);
    }
    span.divider {
        width: 100%;
        max-width: 0.3rem;
        display:inline-block;
        transform:scaleY(1.2); /* W3C */
        -webkit-transform:scaleY(1.2); /* Safari and Chrome */
        -moz-transform:scaleY(1.2); /* Firefox */
        -ms-transform:scaleY(1.2); /* IE 9 */
        -o-transform:scaleY(1.2);
        align-self: center;
        color: rgba(144, 150, 173, 0.5);
        @include font($ff-2, 2rem, 400, 1.17em);
    }
}

.nav-menu-wrap {
    display: flex;
    flex-direction: column;
    height: 100%;
    margin: 0;
    padding: 0;
    flex: 1 0 auto;
    align-self: center;
    &.resume {
         margin: auto;
    }
    .nav-menu {
        text-align: right;
        text-transform: uppercase;
        justify-content: center;
        //align-items: center;
        color: rgba(144, 150, 173, 1);
        text-shadow: 0.08rem 0.05rem 0.2rem rgba(0, 0, 0, 0.8);
        transition: text-shadow 0.4s ease-in-out, color 0.4s ease-in-out;
        @include font($ff-2, 1.5rem, 500, 1em);
        span.divider {
            @include font($ff-2, 1.8rem, 500, 1.5em);
            color: rgba(144, 150, 173, 0.7);
        }
        a {
            &:hover,
            &:active {
                color: rgba(230, 235, 245, 1);
                text-shadow: 0.25rem 0.1rem 0.5rem rgba(0, 0, 0, 1);
            }
        }
    }
}

/* ========================== Footer ================================= */

#site-footer {
    width: 100%;
    margin: 0 auto;
    /*min-width: 45rem;*/
    overflow-x: hidden;
    text-align: center;
    white-space: nowrap;
    color: #909090;
    border: 0.15rem solid;
    border-left: 0;
    border-right: 0;
    border-bottom: 0;
    border-image-slice: 2;
    border-image-source: linear-gradient(to right,rgba(144, 150, 173, 0),rgba(144, 150, 173, 0) 5%,rgba(144, 150, 173, 0.3) 12%,rgba(144, 150, 173, 0.3) 88%,rgba(144, 150, 173, 0) 95%,rgba(144, 150, 173, 0) 100%);
    @include font($ff-base, $fs-base, 400);
    @include padding($space-M 0);
    transition: filter 0.5s cubic-bezier(0.74, 0.03, 0.59, 0.93),
                transform 0.5s cubic-bezier(0.7, 0, 0.68, 1),
                opacity 0.5s cubic-bezier(0.7, 0, 0.68, 1);
    &.finish {
        transform: translateY(0) !important;
        opacity: 1 !important;
        filter: blur(0rem) !important;
    }
    &.slide-out {
        transform: translateY(100%) !important;
        opacity: 0 !important;
        filter: blur(1rem) !important;
    }
    &.init {
        opacity: 0;
        transform: translateY(100%);
        filter: blur(1rem);
    }
    &.resume {
        @include padding(2rem 0);
    }
    a {
        color: hsl(195, 50%, 37%);

        &:hover {
            color: hsl(200, 70%, 45%);
        }
    }
}

/* ========== Media Queries - Smaller than 600px =================*/

@media only screen and (max-width: 650px) {
    #site-header {
        @include padding(0.5rem 0);
            > .header-wrap {
                flex-direction: column;
                text-align: center;
                > .header-title-wrap {
                    align-items: center;
                }
                //> .nav-menu-wrap {
                    //@include padding(1rem 0);
                    //.nav-menu {
                        //text-align: center;
                        //align-self: center;
                    //}
                //}
            }
        //}
    }
}
