@use 'forward' as *;

/* ======================== Animations =================================== */

.remove-header-padding {
    padding: 0 !important;
}

.slow-transition {
    transition: width .5s ease-in-out,
                max-width .5s ease-in-out,
                background .4s cubic-bezier(0.7, 0, 0.68, 1),
                padding .3s ease-in-out,
                box-shadow 0.3s cubic-bezier(0.7, 0.04, 0.6, 0.8) !important;
}

.condense {
    line-height: 0.85em !important;
    &.header-wrap {
        @include padding(0.5rem 0);
    }
    &#footer {
        @include padding(1.3rem 0);
    }
}

@media only screen and (max-width: 650px) {
    #site-header,
    .header-wrap,
    .nav-menu {
        &.slow-transition {
            transition: padding 0 ease;
        }
    }
}

@media only screen and (min-width : 1800px) {

    /* Styles */
    #site-header.remove-header-padding {
        background: rgba(0, 0, 0, 0);
        &.finish {
            box-shadow: none !important;
            &.resume {
                box-shadow: rgba(0, 0, 0, 0.6) 0 2rem 3rem -1rem,
                            rgba(0, 0, 0, 0.29) 0.6rem 0.2rem 1.6rem 0 !important;
                background: rgba(22, 18, 24, 1) !important;
            }
        }
    }

    .expansion {
        width: 100% !important;
        max-width: 98% !important;
        padding: 2.5rem !important;
        & .site-title {
            filter: contrast(100%) !important;
            text-shadow: 0.1rem 0.08rem 0.4rem rgba(0, 0, 0, 0.4) !important;
            &:hover,
            &:active {
                color: rgba(125, 130, 160, 1);
                text-shadow: 0.1rem 0.08rem 0.4rem rgba(0, 0, 0, 0.4) !important;
            }
        }
        & .site-tagline {
            filter: contrast(150%) !important;
            text-shadow: 0.05rem 0.05rem 0.2rem rgba(0, 0, 0, 0.35) !important;
        }
        & .nav-menu {
            filter: contrast(150%) !important;
            text-shadow: 0.05rem 0.05rem 0.2rem rgba(0, 0, 0, 0.35) !important;
            a {
                &:hover,
                &:active {
                    color: rgba(125, 115, 145, 1);
                    text-shadow: 0.05rem 0.05rem 0.3rem rgba(0, 0, 0, 0.42) !important;
                }
            }
        }
    }
}
