@use '../node_modules/rfs/scss' as *;

@mixin font-face($font-name, $file-path, $weight: normal, $style: normal) {
    @font-face {
        font-family: $font-name;
        font-weight: $weight;
        font-style: $style;

        src: url('#{$file-path}.woff2') format('woff2'), url('#{$file-path}.woff') format('woff');
        font-display: swap;
        unicode-range: U+0000-00FF;
    }
}

@mixin font($family, $size, $weight: normal, $line-height: 1.5em, $style: normal) {
    font-family: $family;
    font-weight: $weight;
    font-style: $style;
    line-height: $line-height;

    @include font-size($size);
}

