@use 'forward' as *;

/* ============================ Base Styles =============================== */

html {
    font-size: 62.5%;
    height: 100%;
    scroll-behavior: smooth;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-width: auto;
    scrollbar-color:$c-scroll-thumb $c-scroll;
    scrollbar-gutter: stable;
}

*, *::before, *::after {
    box-sizing: inherit;
}

html,
body {
    margin: 0;
    padding: 0;
    width: 100%;
    /*max-width: 100vw;*/
    box-sizing: border-box;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    color: #666;
    background: linear-gradient(0deg, rgba(196,195,215,1) 0%, rgba(212,213,225,1) 10%, rgba(226,228,236,1) 27%, rgba(240,241,244,1) 53%, rgba(243,244,246,1) 100%);
    background-attachment: fixed;
    background-size: cover;
    @include rfs(inset 0 -1.5rem 3rem 0 #b4b3c7, box-shadow);
    @include font($ff-base, $fs-base, 400, 1.5em);
    &.no-js {
        #site-header,
        .header-wrap,
        .nav-menu,
        #site-footer,
        .content {
            &.init {
                transform: none;
                opacity: 1;
                filter: blur(0rem);
            }
        }
    }
}

body::-webkit-scrollbar {
  width: auto;
  background-color: $c-scroll;
}
body::-webkit-scrollbar-track {
  background-color: $c-scroll;
}
body::-webkit-scrollbar-track-piece {
  background-color: $c-scroll;
}
body::-webkit-scrollbar-thumb {
  background-color: $c-scroll-thumb;
  border-radius: 1.5rem;
}

main {
    flex: 1;
}

.content {
    display: flex;
    flex-direction: column;
    width: 80%;
    max-width: 90rem;
    min-height: 100%;
    color: $c-txt;
    align-items: stretch;
    justify-content: center;
    @include margin($space-L auto);

    transition: filter 0.55s ease-in-out,
                opacity 0.45s cubic-bezier(0.7, 0, 0.68, 1),
                transform 0.6s cubic-bezier(0.77, 0.03, 0.54, 0.92);
    &.finish {
        opacity: 1 !important;
        filter: blur(0rem) !important;
        }
    &.fade-out {
        opacity: 0 !important;
        filter: blur(0.5rem) !important;
        }
    &.init {
        opacity: 0;
        filter: blur(1rem);
        }
    &.resume {
        width: 100%;
        margin: auto;
        display: flex;
        justify-content: center;   /* horizontally centre */
        @include padding(0 1.5rem);
    }
    &.project {
        @include margin($space-M auto);
    }
}

img {
    width: 100%;
    height: auto;
    border-style: none;
    transition: transform 0.5s cubic-bezier(0.77, 0.03, 0.54, 0.92);
    &:not([src]):not([srcset]) {
        visibility: hidden;
    }
}

picture,
iframe,
img {
    display: block;
}

/* Fade-in effect */
img.lazy,
iframe.lazy {
    opacity: 0;
    transition: box-shadow 0.7s ease, opacity 0.5s ease-in-out;
    pointer-events: none;
    &.initial,
    &.loaded,
    &.lazyloaded,
    &.error {
        opacity: 1;
    }
    &.ll-fade {
        box-shadow: none;
        &.vis {
            box-shadow: rgba(38, 57, 77, 0.83) 0px 20px 30px -10px,
                        rgba(136, 165, 191, 0.45) 6px 2px 16px 0px;
        }
    }
}

img:not(.initial) {
    transition: opacity .5s ease-in-out;
}

::-moz-selection {
    color: #222;
    background: #d6edff;
    text-shadow: none;
}

::selection {
    color: #222;
    background: #d6edff;
    text-shadow: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1em;

    margin-top: 0;

    text-rendering: optimizeLegibility;
}

h1 {
    @include font($ff-1, $fs-head, 700, 1.2em);
}

h2 {
    @include font($ff-1, $fs-mid, 700, 1.2em);
}

h3 {
    @include font($ff-1, 1.6rem, 400, 1.2em);
}

h4 {
    @include font($ff-1, 1.4rem, 400, 1.2em);
}

h5 {
    @include font($ff-1, 1.2rem, 400, 1.2em);
}

h6 {
    @include font($ff-1, 1.5rem, 400, 1.2em);
}

a {
    color: rgba(130, 150, 180, 1);
    transition: color .4s ease;
}

a,
a:visited,
a:active,
a:focus,
a:link,
a:hover {
    text-decoration: none;
}

p,
ul,
ol {
    @include margin(1.6rem 0);
}

ol ol,
ul ul,
ul ol,
ol ul {
    @include margin(0.6rem 0);
}

hr {
    display: block;

    height: 0.1rem;
    padding: 0;

    color: rgba(209, 208, 222, 0.25);
    mix-blend-mode: multiply;
    @include margin(1rem 0);
}

@media only screen and (max-width: 650px) {
    body,
    html {
        min-width: 100% !important;
    }
}
